.training {
	height: 780px;
	margin-top: 50px;
}

.steps-list__item {
	display: inline-block;
	width: calc(100% / 3 - 3px);
	text-align: center;
	vertical-align: top;
	padding: 0 20px 5px;
	position: relative;
}

.steps-list__item-img {
	height: 262px;
}

.steps-list__item-text {
	font-family: 'Marck Script',cursive;
	font-size: 24px;
}