.programs {
	margin-top: 35px;
}

.h2-title {
	font-size: 24px;
	text-transform: uppercase;
}

.h2-title_yellow {
	background-color: $yellow;
	font-weight: bold;
	display: inline-block;
	padding: 5px 15px;
}

.programs-container {
	display: inline-block;
}

.programs-container_full-w {
	width: 100%;
}

.programs-container_half-w {
	width: calc(50% - 9px);

	.programs-list__item {
		width: calc(100% / 2 - 22px);
	}

	.h3-title {
		width: calc(100% - 4px);
	}
}

.programs-container_half-w_l {
	margin-right: 13px;
}

.h3-title {
	width: 100%;
	font-size: 24px;
	text-transform: uppercase;
	background-color: #f4f1f2;
	font-weight: bold;
	display: inline-block;
	padding: 5px 15px;
}

.programs-list {
	margin-left: -18px;
}

.programs-list__item {
	// background: url('../img/content/course_yubka.jpg') no-repeat;
	background-position: center;
	background-size: cover;
	height: 370px;
	margin-left: 18px;
	margin-bottom: 16px;
	width: calc(100% / 4 - 22px);
	display: inline-block;
	vertical-align: top;


	&:hover {
		background-image: none !important;
		background: linear-gradient( 0deg, #f7e715 0%, #fbf554 100%) !important;
		text-align: center;
		background-position: center;

		.programs-list__item-container_block {
			position: relative;
			top: 50%;
			transform: translateY(-50%);
		}

		.programs-list__item-span_1,
		.programs-list__item-span_2 {
			color: #151521;
		}

		.programs-list__item-span_3 {
			display: none;
		}

		.programs-list__item-span_4 {
			display: none;
		}
	}
}

.programs-list__item-container {
	margin: 7px;
	border: 2px dashed #fff;
	width: calc(100% - 14px);
	height: calc(100% - 14px);
	padding: 12px;
	display: block;
}

.programs-list__item-span {
	display: block;
	font-size: 26px;
	line-height: 1;
}

.programs-list__item-span_1 {
	color: $purple;
	white-space: pre-line;
}

.programs-list__item-span_2 {
	color: $purple-dark;
	margin: 10px 0 40px 0;
}

.programs-list__item-span_2,
.programs-list__item-span_3 {
	text-transform: uppercase;
	font-weight: bold;
}

.programs-list__item-span_3,
.programs-list__item-span_4 {
	font-size: 16px;
	color: #fff;
	white-space: pre-line;
}

.programs-more {
	text-align: center;
}

.programs-more__text {
	width: 830px;
	margin: 20px auto 30px;
	font-size: 24px;
	font-family: 'Marck Script',cursive;
}

.more__button {
	display: inline-block;
	text-align: center;
	padding-top: 16px;
	margin-bottom: 30px;
}