.decor-form {
	position: relative;
	height: 415px;
	background: url('../img/decor/strip-pattern.png') center no-repeat;
	background-size: cover;
}

.decor-pic {
	position: absolute;
	left: 50%;
	transform: translateX(-50%);
}

.more-form__container_decor-form {
	position: absolute;
	left: 50%;
	transform: translateX(-50%);
	background: linear-gradient( 0deg, #c3b4c5 0%, #c2b4c4 100%);

	.more-form {
		color: #000;
	}

	.button {
		color: $yellow;
		background: linear-gradient( 0deg, #561e4a 0%, #a36798 100%);

		&:hover {
			background: linear-gradient( 0deg, #a36798 0%, #561e4a 100%);
		}
	}
}