.header {
	height: 195px;
}

.header-nav {
	height: 44px;
	background-color: $purple-dark;
	box-shadow: 0px 5px 5px 0px rgba(0, 0, 0, 0.26);
	position: relative;
}

.nav-list {
	text-align: justify;

	&:after {
		content: '';
		display: inline-block;
		width: 100%;
		height: 0;
	}
}

.nav-item {
	display: inline-block;
	vertical-align: middle;
}

.nav-item__link {
	display: block;
	color: #fff;
	font-weight: 300;
	padding: 9px 40px 10px 30px;
	transition: all .5s;

	&:hover {
		color: $yellow;
		text-decoration: underline;
	}
}

.header-main {
	height: 150px;

	.container {
		@extend %clearfix;
	}
}

.header-main_gradient {
	/* Permalink - use to edit and share this gradient: http://colorzilla.com/gradient-editor/#8d347a+0,aa5399+100 */
	background: #8d347a; /* Old browsers */
	background: -moz-linear-gradient(top,  #8d347a 0%, #aa5399 100%); /* FF3.6-15 */
	background: -webkit-linear-gradient(top,  #8d347a 0%,#aa5399 100%); /* Chrome10-25,Safari5.1-6 */
	background: linear-gradient(to bottom,  #8d347a 0%,#aa5399 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
	filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#8d347a', endColorstr='#aa5399',GradientType=0 ); /* IE6-9 */
}

.logo,
.title,
.contacts {
	float: left;
}

.logo {
	width: 200px;
	margin-top: 20px;
}

.title {
	width: calc((100% - 200px) / 1.5);
}

.contacts {
	width: calc((100% - 200px) / 3);
	color: #fff;
	text-align: right;
	margin-top: 25px;
}

.h1-title {
	font-family: 'Marck Script', cursive;
	color: #fff;
	font-size: 40px;
	margin-bottom: 0;
	padding: 20px 60px 0 60px;
	text-align: center;
	position: relative;

	&:after {
		content: '';
		display: block;
		background: url('../img/decor/title-pattern.png') center no-repeat;
		width: 100%;
		height: 100%;
		position: absolute;
		left: 0;
		top: 15px;
	}
}

.contacts-tel {
	font-weight: bold;
	font-size: 26px;
}

.adr {
	font-size: 16px;

	.fa {
		margin-right: 10px;
	}
}

.contacts-link {
	color: #fff;
	transition: all .5s;

	&:hover {
		text-decoration: underline;
		color: $yellow;
	}
}