.final {
	background: url('../img/bg/teachers-bg.png') center no-repeat;
	background-size: cover;
	padding: 140px 0 40px;
	margin-top: -90px;
}

.programs-list__item-span_final {
	color: $purple-dark;
	font-size: 24px;
	white-space: pre-line;
}

.programs-list__item-span_final_bold {
	font-weight: bold;
	text-transform: uppercase;
}

.programs-list__item {
	&:hover {
		.programs-list__item-span_final,
		.programs-list__item-span_final_bold {
			color: #151521;
		}
	}
}