.training-process {
	background: url('../img/bg/training-process-bg_clear.jpg') center no-repeat;
	background-size: cover;
	height: 730px;
	padding: 55px 0;
	position: relative;

	.container {
		position: relative;
		top: 50%;
		transform: translateY(-50%);

		.h2-title {
			margin-bottom: 50px;
			color: #fff;

			.h2-title_yellow {
				color: #000;
			}
		}
	}
}

.slider {
	position: relative;
	margin: 0 auto;
	width: 730px;
	height: 400px;
	border: 7px solid #fff;
}

.slider-container {
	overflow: hidden;
}

.slider-list {
	white-space: nowrap;
	height: 100%;
	position: relative;
	transform: translateX(0);
}

.slider-list__item {
	display: inline-block;
	width: 716px;
	text-align: center;
	white-space: nowrap;
	font-size: 0;
	vertical-align: top;
	margin-left: -4px;

	&:first-child {
		margin-left: 0;
	}
}

.slider-list__item-img {
	max-width: 716px;
	max-height: 400px;
	height: calc(400px - 14px);
}

.slider-controls {
	position: absolute;
	top: 50%;
	transform: translateY(-50%);
	margin-top: -32px;
	left: 0;
	right: 0;
}

.slider-controls__button {
	background: url('../img/decor/slider-controls.png') center no-repeat;
	background-position: 0 0;
	background-size: cover;
	display: block;
	width: 64px;
	height: 64px;
	font-size: 0;
	cursor: pointer;
	position: absolute;


	&.right {
		background-position: -210px 0;
		right: -90px;
		
		&:hover {
			background-position: -315px 0;
		}
	}

	&.left {
		left: -90px;

		&:hover {
			background-position: -105px 0;
		}
	}	
}