.welcome {
	height: 550px;
	background: url('../img/bg/welcome-bg.png') center no-repeat;
	background-size: cover;
}

.slogan {
	width: 550px;
	text-transform: uppercase;
	position: absolute;
	top: 60px;
}

.slogan-1 {
	font-weight: bold;
	color: #fff;
	font-size: 45px;
	display: inline-block;
	padding: 5px 25px;
}

.slogan-1_opacity-81 {
	background: rgba($purple-dark, .81);
}

.slogan-1_opacity-51 {
	background: rgba($purple-dark, .51);
}

.slogan-2 {
	color: #404041;
	font-size: 24px;
	display: inline-block;
	padding: 5px 25px;
	margin-top: 6px;
	background-color: rgba(#f3d8a2, .89);
}

.more-form__container {
	width: 300px;
	height: 415px;
	background-color: rgba(127, 60, 114, .522);
}

.more-form__container_header {
	position: absolute;
	right: 0;
	top: 30px;
}

.more-form {
	margin: 9px;
	width: calc(100% - 18px);
	height: calc(100% - 18px);
	border: 1px dashed #fff;
	color: #fff;
	text-align: center;
	padding-top: 15px;
}

.more-form__title {
	font-size: 22px;
	text-transform: uppercase;
	font-weight: bold;
	margin-bottom: 5px;
	white-space: pre-line;
}

.more-form__text {
	font-size: 18px;
	white-space: pre-line;
	margin-bottom: 5px;
	font-weight: 300;
}

.more-form__input-text {
	height: 50px;
	width: 260px;
	border: none;
	border-radius: 5px;
	box-shadow: inset 0px 3px 2px 0px rgba(0, 0, 0, 0.15);
	font-size: 18px;
	padding: 0 15px;
	margin-bottom: 5px;
	position: relative;
	color: #955e8a;

	&::-webkit-input-placeholder {
		color:#955e8a;
	}

	&::-moz-placeholder {
		color:#955e8a;
	}

	&:-moz-placeholder {
		color:#955e8a;
	}

	&:-ms-input-placeholder {
		color:#955e8a;
	}
}

.button {
	border: none;
	color: #404041;
	font-size: 18px;
	text-transform: uppercase;
	width: 260px;
	height: 50px;
	border-radius: 5px;
	background: linear-gradient( 0deg, #f7e715 0%, #fbf554 100%);
	box-shadow: 0px 4px 0px 0px rgba(0, 0, 0, 0.34);

	&:hover {
		background: linear-gradient( 0deg, #fbf554 0%, #f7e715 100%);
	}
}