.teachers {
	background: url('../img/bg/teachers-bg.png') center no-repeat;
	background-size: cover;
	padding: 120px 0 40px;
	margin-top: -55px;
	opacity: 0.99;
}

.teachers-content {
	@extend %clearfix;
}

.teachers-item {
	width: calc(50% - 50px);
	float: left;
	margin-left: 100px;

	&:first-child {
		margin-left: 0;
	}
}

.teachers-item__img,
.teachers-item__text {
	display: inline-block;
	vertical-align: middle;
}

.teachers-item__img {
	width: 250px;
	height: 275px;
}

.teachers-item__text-name {
	font-size: 24px;
	font-weight: bold;
	font-style: italic;
}

.teachers-item__text-position {
	font-style: italic;
	margin-bottom: 14px;
}

.teachers-item__text-experience_bold {
	font-weight: bold;
}