.how-start {
	margin: 55px 0 75px;
}

.steps-list__item-text_num {
	font-size: 30px;
	color: $purple;
	font-style: normal;
	font-weight: bold;
	font-family: 'Roboto', sans-serif;
}

.arrow {
		&:after {
			content: '';
			position: absolute;
			width: 354px;
			height: 51px;
			display: block;
			background: url('../img/decor/arrow-pattern.png') center no-repeat;
			background-size: cover;
			top: 350px;
			left: 230px;
		}
	}