.dress-info {
	background: url('../img/bg/dress-bg.png') center no-repeat;
	background-size: cover;
	margin-top: -40px;
	height: 750px;
	text-align: center;

	.container {
		position: relative;
		top: 50%;
		transform: translateY(-50%);
	}
}

.dress-info__content {
	background-color: #FFF;
	border-radius: 5px;
	display: inline-block;
	padding: 5px;
}

.dress-info__text {
	font-size: 26px;
	font-family: 'Marck Script',cursive;
	border: 1px dashed #f2eef2;
	border-radius: 5px;
	padding: 5px 20px;
}

.dress-info__image {
	margin: 30px 0 40px;
}