.map {
	height: 565px;

	.container {
		top: 130px;
		z-index: 10;

		.h2-title {
			position: absolute;
			top: -75px;
		}
	}
}

.map-container {
	height: 100%;
	width: 100%;
}

.map-content {
	background-color: rgba($purple-dark, 0.75);
	padding: 13px;
	height: 155px;
	width: 490px;
	position: absolute;
	top: 110px;
	left: 50%;
	transform: translateX(-50%);
}

.map-content__text {
	border: 2px dashed $yellow;
	height: 100%;
	white-space: pre-line;
	text-align: center;
	color: #fff;
	font-size: 22px;
	padding-top: 12px;
}

.cont {
	@extend %clearfix;
	margin-bottom: 20px;
}

.copyright {
	clear: both;
	color: #fff;
	font-size: 22px;
	height: 60px;
	border-top: 2px solid $purple-dark;
	padding-top: 12px;
}