.socials {
	padding-top: 45px;
	text-align: center;

	.container {
		@extend %clearfix;
	}
}

.socials-block {
	width: calc(50% - 20px);
	float: left;
}

.socials-inst {
	margin-left: 20px;
}

.vk-pic,
.inst-pic {
	height: 630px;
	margin-bottom: 40px;
}

.vk-pic {
	background: url('../img/decor/socials-vk.jpg') center no-repeat;
	background-size: cover;
}

.inst-pic {
	background: url('../img/decor/socials-inst.jpg') center no-repeat;
	background-size: cover;
}